<template>
  <v-app>
    <v-navigation-drawer
      v-model="drawer"
      app
      class="indigo darken-1"
      dark
    >
      <v-list
        nav
        dense
      >
        <v-list-item-group
          v-model="group"
        >
          <v-list-item>
            <v-avatar color="primary lighten-1">
              <v-icon>
                mdi-account-circle
              </v-icon>
            </v-avatar>
            <v-list-item-content>
              <v-list-item-title class="text-subtitle-3 ml-2">
                  {{user.name}}
              </v-list-item-title>
              <v-list-item-subtitle class="ml-2">
                  {{user.email}}
              </v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>

          <v-divider></v-divider>
          <router-link :to="`/Dashboard/${device}`" style="text-decoration: none; color: white;">
            <v-list-item>
              <v-list-item-title>Dashboard</v-list-item-title>
            </v-list-item>
          </router-link>

          <router-link :to="`/deviceCalib/${device}`" style="text-decoration: none; color: white;">
            <v-list-item>
              <v-list-item-title>Configuration</v-list-item-title>
            </v-list-item>
          </router-link>

          <router-link to="/Register_Device" style="text-decoration: none; color: white;">
            <v-list-item>
              <v-list-item-title>Register Device</v-list-item-title>
            </v-list-item>
          </router-link>

        </v-list-item-group>
      </v-list>

      
      <div class="ma-2">
        <v-btn color="error darken-1" block @click="SignOut">
          Logout
        </v-btn>
      </div>
      
    </v-navigation-drawer>
    <v-app-bar
      app
      color="primary"
      dark
    >
    <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <div class="d-flex align-center">
        <v-img
          alt="FTU Logo"
          class="shrink mr-2"
          contain
          src="@/assets/logo.png"
          transition="scale-transition"
          width="40"
        />
      </div>
      <v-spacer></v-spacer>
      <div>
        <v-select
          v-model="device"
          @change="$router.push(device);"
          :rules="required"
          :items="listOfDevice"
          label="Ombrometer"
          class="mt-4"
          dense
          dark
        >
        </v-select>
      </div>
      

    </v-app-bar>
    <v-main>
      <router-view :key="device"></router-view>
      <v-snackbar
        v-model="snackbar2"
        :vertical="true"
        centered
        color="red darken-2"
        :timeout="-1"
      >
        {{ notif }}

        <template v-slot:action="{ attrs }">
          <v-btn
            color="warning"
            text
            v-bind="attrs"
            @click="snackbar2 = false"
          >
            Close
          </v-btn>
        </template>
      </v-snackbar>
    </v-main>
  </v-app>
</template>

<script>
import moment from 'moment'
import firebase from "firebase/app"
import 'firebase/auth'
import { db }from "../firebase/db.js"

  export default {
    name: 'Logged',

    data: () => ({
      user: {
        initials: '',
        fullName: '',
        email: '',
        name:'',
      },
      time: {
        day:moment(new Date()).format("dddd"),
        time:moment(new Date()).format("HH:mm"),
        date:moment(new Date()).format("DD MMM YYYY")
      },
      newPassword:'',
      dialog: false,
      passwordRules: [
        v => !!v || 'Password is required',
        v => (v && v.length >=8) || 'Password must be at least 8 characters'
      ],
      required: [
        v => !!v || 'This field is required',
      ],
      snackbar:false,
      timeout: 2000,
      drawer: false,
      group: null,
      snackbar2:false,
      notif:'',
      offset:0,
      role:'',
      station:'',
      name:'',
      userRef:null,
      pages:[],
      listOfDevice:[],
      device:null
    }),
    async mounted() { 
      // this.role=localStorage.getItem("role");
      // this.station=localStorage.getItem("station");
      // if (this.role=='Petugas') {
      //   this.MapPetugas=`/MapPetugas-id=${localStorage.getItem("device")}`
      //   this.NotifikasiPetugas=`/NotificationPetugas-id=${localStorage.getItem("device")}`
      //   this.KondisiPetugas=`/DataPetugas-id=${localStorage.getItem("device")}`
      //   this.RekapPetugas=`/RecapPetugas-id=${localStorage.getItem("device")}`
      //   this.ChartPetugas=`/ChartPetugas-id=${localStorage.getItem("device")}`
      // }
      const date=new Date();
      this.offset=date.getTimezoneOffset()/(-60)
      console.log('Requesting permission...');
      await Notification.requestPermission().then((permission) => {
        if (permission === 'granted') {
          console.log('Notification permission granted.');
        }
      })
      this.$messaging.onMessage((payload) => {
        this.snackbar2=true
        this.notif=payload.data.body
        // let notiftext= payload.data.body
        // let textArray = notiftext.split('&')
        // this.notif=''
        // textArray[1]= this.updateTime(textArray[1])
        // for (let i=0;i<textArray.length;i++) {
        //   if (textArray[i]!= undefined) {
        //     this.notif= this.notif.concat(textArray[i])
        //   }
        // }
      });

      await db.collection('Ombrometer').get().then((docs)=> {
        this.listOfDevice=[];
        docs.forEach((doc)=> {
          this.listOfDevice.push({value:doc.id, text:doc.data().station})
          this.device=doc.id
        })
        this.$router.push(`/Dashboard/${this.device}`)
      })
      // this.highlightNavbar(this.$route.name)
      firebase.auth().onAuthStateChanged((user) => {
        if (user) {
          //Signed In
          var uid = user.uid;
          const date=new Date();
          this.$messaging.getToken({ vapidKey: 'BFQo2fD7ySFg-tpGZh3wLwDPCN1zy6GhyzELfKN6igiRH8rDM0-12pdlkxg3wYxeYvQKZKDD6LErtolkinHWPxw' }).then((currentToken)=> {
            if (currentToken) {
              localStorage.setItem("messaging",currentToken)
              db.collection('user').doc(uid).collection('notifToken').doc(currentToken).set({
                token:currentToken,
                time: Math.floor(date.getTime()/1000)
              })
              db.collection('user').doc(uid).get().then((doc) => {
                this.user.email=doc.data().email
                this.user.name= doc.data().name
              })
            } else {
              console.log('No registration token available. Request permission to generate one.');
            }
          }).catch((err)=> {
            console.log(err)
          })
          // if (localStorage.getItem("role")!='Petugas') {
          //   this.user.email=user.email;
          //   this.userRef=db.collection('user').doc(uid).onSnapshot((doc) => {
              // localStorage.setItem("role", doc.data().role);
              // this.role=doc.data().role
              // localStorage.setItem("location", doc.data().location);
              // db.collection('roleSetting').doc(this.role).get().then((doc) => {
              //   this.pages=[]
              //   doc.data().page.forEach((path) => {
              //     if (path =='Map'){
              //       this.pages.push({path:'/', page:'Peta'})
              //     } else if (path =='MapGPS'){
              //       this.pages.push({path:'/MapGPS', page:'Peta GPS'})
              //     } else if (path == 'Condition') {
              //       this.pages.push({path:path, page:'Tabel Kondisi'})
              //     } else if (path == 'History') {
              //       this.pages.push({path:path, page:'Tabel Historis'})
              //     } else if (path == 'AddDevice') {
              //       this.pages.push({path:path, page:'Add Device'})
              //     } else if (path == 'Administrator') {
              //       this.pages.push({path:path, page:'Administrator'})
              //     } else if (path == 'AdminTMA') {
              //       this.pages.push({path:path, page:'Administrator TMA'})
              //     } else if (path == 'Recap') {
              //       this.pages.push({path:path, page:'Rekapitulasi'})
              //     } else if (path == 'Chart') {
              //       this.pages.push({path:path, page:'Grafik Rekapitulasi'})
              //     } else if (path == 'ChartTMA') {
              //       this.pages.push({path:path, page:'Grafik TMA'})
              //     } else if (path == 'Notification') {
              //       this.pages.push({path:path, page: 'Notifikasi'})
              //     } else if (path == 'NotificationTMA') {
              //       this.pages.push({path:path, page: 'Notifikasi TMA'})
              //     } else if (path =='DownloadFile'){
              //       this.pages.push({path:'/DownloadFile', page:'Log File'})
              //     } else if (path =='DownloadFileMonthly'){
              //       this.pages.push({path:'/DownloadFileMonthly', page:'Log File Bulanan'})
              //     }
              //   })
              // })
          //     if (doc.data().confirmed !==false) {
          //         console.log(doc.data().role)
          //     } else {
          //         this.$router.push('confirm')
          //     }
          //   })
          // }
          
        } else {
          //Signed Out
          let uid=localStorage.getItem('uid')
          let messaging = localStorage.getItem('messaging')
          if ((messaging!=null)&&(uid!=null)) {
            db.collection("user").doc(uid).collection('notifToken').doc(messaging).delete().then(() => {
              console.log("Document successfully deleted!");
              localStorage.removeItem('uid')
              localStorage.removeItem('station')
              localStorage.removeItem('role') 
              localStorage.removeItem('location') 
              localStorage.removeItem('device') 
              this.$router.replace('/login')
            }).catch((error) => {
                console.error("Error removing document: ", error);
            });
          } else {
            localStorage.removeItem('uid')
            localStorage.removeItem('station')
            localStorage.removeItem('role') 
            localStorage.removeItem('location') 
            localStorage.removeItem('device') 
            this.$router.replace('/login')
          }
          
        }
      });
    },
    watch : {
      group () {
        this.drawer = false
      },
    },
    methods: {
      updateTime (time) {
        return moment.unix(time).utcOffset(this.offset).format("HH:mm, DD MMM YYYY")
      },
      async SignOut() {
        //Do Something to Sign Out
        let uid=localStorage.getItem('uid')
        let messaging = localStorage.getItem('messaging')
        if ((messaging!=null)&&(uid!=null)) {
          await db.collection("user").doc(uid).collection('notifToken').doc(messaging).delete().then(() => {
              console.log("Document successfully deleted!");
          }).catch((error) => {
              console.error("Error removing document: ", error);
          });
        }
        firebase.auth().signOut().then(() => {
          // Sign-out successful.
          localStorage.removeItem('uid')
          localStorage.removeItem('station')
          localStorage.removeItem('role') 
          localStorage.removeItem('location') 
          localStorage.removeItem('device') 
          localStorage.removeItem('messaging') 
          this.$router.replace('/login')
        }).catch((error) => {
          // An error happened.
          console.log(error)
        });
      },
      ChangePassword(newPassword) {
        //Show modal to change password
        const user = firebase.auth().currentUser;
        user.updatePassword(newPassword).then(() => {
          // Update successful.
          this.dialog=false
          this.snackbar=true
        }).catch((error) => {
          // An error ocurred
          // ...
          console.log(error)
        });
      },
      highlightNavbar(route) {
        if (route == 'Map') {
          this.group=1
        } else if (route=='Condition') {
          this.group=2
        } else if (route=='History') {
          this.group=3
        } else if (route=='Notification') {
          this.group=4
        } else if (route=='AddDevice') {
          this.group=5
        } else if (route=='Recap') {
          this.group=6
        } else if (route=='Administrator') {
          this.group=7
        }else if (route =='deviceCalib'){
          this.group=8
        } else {
          this.group=9
        }
      }
    }
  }
</script>